import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import OpenSourceIncubator from "./views/OpenSourceIncubator";
import Community from "./views/Community";
import StartupIncubator from "./views/StartupIncubator";

Vue.use(Router);

export default new Router({
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/",
            name: "home",
            component: Home
        },
        {
            path: "/startup-incubator",
            name: "startup-incubator",
            component: StartupIncubator
        },
        {
            path: "/community",
            name: "community",
            component: Community
        },
        {
            path: "/open-source-incubator",
            name: "open-source-incubator",
            component: OpenSourceIncubator
        }
    ],
    scrollBehavior (to, from, savedPosition) {
        return { x: 0, y: 0 };
    }
});
